var wx = require("weixin-js-sdk");
import service from '@/http/services/user.js'
export default {
  name: "Share",
  props: ["share"],
  data () {
    return {
      shareInfo: {
        title: "", // 
        desc: "100", // 
        link: "https://mobile.kaoben.top/#/Register?invite=" + this.$cookies.get('userid'), // 
        imgUrl: "https://kaoben.top/images/logo.png", // 
        type: "", // ,musicvideolinklink
        dataUrl: "", // typemusicvideo
      }
    }
  },
  mounted() {
    service.userService.getShareConfig().then(res => {
      if (res.status === 200 && res.data.status === 0) {
        wx.config({
          debug: true, //
          appId: res.data.config.appId, //
          timestamp: res.data.config.timestamp, //
          nonceStr: res.data.config.nonceStr, //
          signature: res.data.config.signature, //
          jsApiList: ["onMenuShareAppMessage", "onMenuShareTimeline"] //js
        });
      }
    });
  },
  methods: {
    hideModal() {
      this.$emit("changeShare", 0);
    },
    //
    onMenuShareAppMessage() {
      wx.onMenuShareAppMessage({
        title: this.shareInfo.title,
        desc: this.shareInfo.desc,
        link: this.shareInfo.link,
        imgUrl: this.shareInfo.imgUrl,
        type: "",
        dataUrl: "",
        success: function() {
          // 
          alert("");
        },
        cancel: function() {
          // 
          alert("");
        }
      });
    },
    //
    onMenuShareTimeline() {
      wx.onMenuShareTimeline({
        title: this.shareInfo.title,
        desc: this.shareInfo.desc,
        link: this.shareInfo.link,
        imgUrl: this.shareInfo.imgUrl,
        success: function() {
          // 
        },
        cancel: function() {
          // 
        }
      });
    }
  }
};



