import Vue from 'vue'
import Router from 'vue-router'
import main from '@/pages/main'
import Login from '@/pages/login/Login' //
import Register from '@/pages/login/Register' //
import VerificationCode from '@/pages/login/VerificationCode' //
import home from '@/pages/main/home/index' //
import SetMeal from '@/pages/main/package/SetMeal' //
import SetMealDetail from '@/pages/main/package/SetMealDetail' //
import ClassQuality from '@/pages/main/course/ClassQuality' //
import ClassShare from '@/pages/main/course/ClassShare' //
import PublicClass from '@/pages/main/open/PublicClass' //
import Library from '@/pages/main/library/Library' //
import Notice from '@/components/Notice' //
import LibraryDetail from '@/pages/main/library/LibraryDetail' //
import InformationList from '@/pages/main/information/informationList' //
import InformationDetail from '@/pages/main/information/informationDetail' //
import PayCenter from '@/pages/main/pay/PayCenter'  //
import PayOk from '@/pages/main/pay/PayOk'  //
import UserCenter from '@/pages/main/user/UserCenter'   // 
import MyClass from '@/pages/main/user/MyClass'   // 
import MyClassDetail from '@/pages/main/user/MyClassDetail'   // 
import Message from '@/pages/main/user/Message'//
import MessageDetail from '@/pages/main/user/MessageDetail'//
import MyGold from '@/pages/main/user/MyGold'//
import MyOrder from '@/pages/main/user/MyOrder'//
import MyCollect from '@/pages/main/user/MyCollect'//
import MyEarnings from '@/pages/main/user/MyEarnings'//
import MyGeneralize from '@/pages/main/user/MyGeneralize'//
import EditInfo from '@/pages/main/user/EditInfo'//
import MyPublicity from '@/pages/main/user/MyPublicity'//
import MySet from '@/pages/main/user/MySet'//
import QualityCourseVideo from '@/pages/main/course/QualityCourseVideo'//

// 
import MarketerCenter from '@/pages/marketer/MarketerCenter' //
import MarketEarnings from '@/pages/marketer/MarketEarnings' // 
import MyFile from '@/pages/marketer/MyFile' // 
import SubordinateList from '@/pages/marketer/SubordinateList' // 
import AddSubordinate from '@/pages/marketer/AddSubordinate' // 
import EditSubordinate from '@/pages/marketer/EditSubordinate' // 
import MarketGeneralize from '@/pages/marketer/MarketGeneralize' // 

// 
import TeacherCenter from '@/pages/teacher/TeacherCenter' //
import TeacherEarnings from '@/pages/teacher/TeacherEarnings' //
import TeacherGeneralize from '@/pages/teacher/TeacherGeneralize' //
import TeacherClass from '@/pages/teacher/TeacherClass' //

import Address from '@/components/Address' //
import OpenCoursePayCenter from '@/components/OpenCoursePayCenter' //

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [{
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/VerificationCode',
      name: 'VerificationCode',
      component: VerificationCode
    },
    {
      path: '/',
      name: 'main',
      component: main,
      children: [{
          path: '/index',
          name: 'index',
          component: home
        },
        {
          path: '/SetMeal',
          name: 'SetMeal',
          component: SetMeal
        },
        {
          path: '/PublicClass',
          name: 'PublicClass',
          component: PublicClass
        },
        {
          path: '/Library',
          name: 'Library',
          component: Library
        },
        {
          path: '/ClassQuality',
          name: 'ClassQuality',
          component: ClassQuality
        }
      ]
    },
    // 
    {
      path: '/SetMealDetail/:pid',
      name: 'SetMealDetail',
      component: SetMealDetail
    },
    // 
    {
      path: '/ClassShare/:id',
      name: 'ClassShare',
      component: ClassShare
    },
    {
      path: '/InformationDetail/:id',
      name: 'InformationDetail',
      component: InformationDetail
    },
    {
      path: '/informationList',
      name: 'informationList',
      component: InformationList
    },
    {
      path: '/LibraryDetail/:id',
      name: 'LibraryDetail',
      component: LibraryDetail
    },
    {
      path: '/PayCenter/:type/:id',
      name: 'PayCenter',
      component: PayCenter
    },
    {
      path: '/PayOk/:id',
      name: 'PayOk',
      component: PayOk
    },
    {
      path: '/UserCenter/',
      name: 'UserCenter',
      component: UserCenter
    },
    {
      path: '/Message/',
      name: 'Message',
      component: Message
    },
    {
      path: '/MessageDetail/:read_id',
      name: 'MessageDetail',
      component: MessageDetail
    },
    {
      path: '/MyClass/',
      name: 'MyClass',
      component: MyClass
    },
    {
      path: '/MyClassDetail/:id',
      name: 'MyClassDetail',
      component: MyClassDetail
    },
    {
      path: '/MyGold/',
      name: 'MyGold',
      component: MyGold
    },
    {
      path: '/MyOrder/',
      name: 'MyOrder',
      component: MyOrder
    },
    {
      path: '/MyCollect/',
      name: 'MyCollect',
      component: MyCollect
    },
    {
      path: '/MyEarnings/',
      name: 'MyEarnings',
      component: MyEarnings
    },
    {
      path: '/MyGeneralize/',
      name: 'MyGeneralize',
      component: MyGeneralize
    },
    {
      path: '/EditInfo/',
      name: 'EditInfo',
      component: EditInfo
    },
    {
      path: '/MyPublicity/',
      name: 'MyPublicity',
      component: MyPublicity
    },
    {
      path: '/MySet/',
      name: 'MySet',
      component: MySet
    },
    {
      path: '/QualityCourseVideo/',
      name: 'QualityCourseVideo',
      component: QualityCourseVideo
    },
    {
      path: '/Address',
      name: 'Address',
      component: Address
    },
    {
      path: '/Notice/:id',
      name: 'Notice',
      component: Notice
    },
    // -
    {
      path: '/MarketerCenter/',
      name: 'MarketerCenter',
      component: MarketerCenter
    },
    // -
    {
      path: '/MarketEarnings/',
      name: 'MarketEarnings',
      component: MarketEarnings
    },
    // -
    {
      path: '/MyFile/',
      name: 'MyFile',
      component: MyFile
    },
    // -
    {
      path: '/MarketGeneralize/:month',
      name: 'MarketGeneralize',
      component: MarketGeneralize
    },
    // -
    {
      path: '/SubordinateList/',
      name: 'SubordinateList',
      component: SubordinateList
    },
    // -
    {
      path: '/AddSubordinate/',
      name: 'AddSubordinate',
      component: AddSubordinate
    },
    // -
    {
      path: '/EditSubordinate/:id',
      name: 'EditSubordinate',
      component: EditSubordinate
    },
    // 
    {
      path: '/TeacherCenter/',
      name: 'TeacherCenter',
      component: TeacherCenter
    },
    {
      path: '/TeacherEarnings/',
      name: 'TeacherEarnings',
      component: TeacherEarnings
    },
    {
      path: '/TeacherGeneralize/:month',
      name: 'TeacherGeneralize',
      component: TeacherGeneralize
    },
    {
      path: '/TeacherClass',
      name: 'TeacherClass',
      component: TeacherClass
    },



    {
      path: '/OpenCoursePayCenter/:id',
      name: 'OpenCoursePayCenter',
      component: OpenCoursePayCenter
    },
  ]
})

// 
router.beforeEach((to, from, next) => {
  if (to.name === 'main') {
    router.push({
      name: 'index'
    })
    return false;
  }
  let isLogin = global.$cookies.get('isLogin'); // 
  // loginhome
  if (to.name === 'Login' || to.name === 'Register') {
    if (isLogin) {
      router.push({
        name: 'index'
      });
      return false;
    }
  }
  // to: Route:  
  // from: Route: 
  // next: Function:  resolve  next 
  // ABBAscrollTop0
  window.scroll(0, 0);
  // studentNextRoute: 
  const studentNextRoute = ['UserCenter', 'MyClass', 'MyClassDetail', 'MyGold', 'MyOrder', 'MyCollect', 'MyEarnings', 'MyGeneralize', 'PayCenter'];
  // teacherNextRoute: 
  const teacherNextRoute = ['TeacherCenter',  'TeacherEarnings', 'TeacherGeneralize']
  // marketerNextRoute: 
  const marketerNextRoute = ['MarketerCenter', 'MarketEarnings', 'SubordinateList', 'AddSubordinate', 'EditSubordinate']
  // nextRoutelogin
  let role = global.$cookies.get("access_role");
  if (studentNextRoute.includes(to.name) || teacherNextRoute.includes(to.name) || marketerNextRoute.includes(to.name)) {
    if (!isLogin) {
      router.push({
        name: 'Login'
      })
      return false;
    }
  }
  if (role) {
    if (role == 'student') {
      if (teacherNextRoute.includes(to.name) || marketerNextRoute.includes(to.name)) {
        router.push({
          name: 'index'
        });
        return false;
      }
    } else if (role == 'teacher') {
      if (studentNextRoute.includes(to.name) || marketerNextRoute.includes(to.name)) {
        router.push({
          name: 'TeacherCenter'
        });
        return false;
      }
    } else if (role.indexOf('marketer') > -1) {
      if (studentNextRoute.includes(to.name) || teacherNextRoute.includes(to.name)) {
        router.push({
          name: 'MarketerCenter'
        });
        return false;
      }
    } else if (studentNextRoute.includes(to.name) || teacherNextRoute.includes(to.name) || marketerNextRoute.includes(to.name)) {
      router.push({
        name: 'index'
      });
      return false;
    }
  } else if (teacherNextRoute.includes(to.name) || marketerNextRoute.includes(to.name)) {
    router.push({
      name: 'index'
    });
    return false;
  }
  next();
});

export default router;



